.close {
    position: relative;
    height: 160px;
    width: 160px;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.close:before,
.close:after {
    position: absolute;
    content: '';
    width: 105%;
    top:19px;
    left:-12px;
    font-weight:bold;

    /* height: 1.8px; */
    height: 6px;
    background-color: #86b1b6;
}

.close:before {
    transform: rotate(45deg);
}

.close:after {
    transform: rotate(-45deg);
}